export default {
	name: 'ranking-item',
	props: {
		list: {
			// 要显示的数组
			type: Array,
			default () {
				return [];
			}
		}
	},
	methods: {
		chooseRank(obj) {
			this.$emit("setRank", obj);
		},
		linkTo(key) {
			let url = ''
			switch (key) {
				case 1:
					url = '/pages/ranking/friends'
					break;
				case 2:
					url = '/pages/ranking/post'
					break;
				case 3:
					url = '/pages/ranking/invitation'
					break;
				case 4:
					url = '/pages/ranking/product'
					break;
				case 5:
					url = '/pages/ranking/product'

					break;
				default:
					break;
			}
			uni.navigateTo({
				url: url
			})
		}
	}
}
